import React, { useState } from 'react'
import classes from './index.module.scss'
import { Typography } from '@mui/material'
import Autocomplete from 'react-autocomplete'
import items from '../inv.json'

export default function SeachPage(){
    const [selected, setSelected] = useState(null)
    const [selectedName, setSelectedName] = useState('')

	return (
        <div className={classes.SearchBody}>
            <h2 className={classes.title}>IMPEL</h2>
            <div className={classes.centerContent}>
                <Typography variant="h3" align="center" pt={"11vh"}>COMPARE VEHICLE</Typography>
                <Typography variant="p" align="center" pt={"0.5vh"} display="block">DESCRIPTION</Typography>
                <br /><br />
                <div className={classes.inputs}>
                    <div className={classes.vs}>
                        <div></div>
                        <span>vs</span>
                        <div></div>
                    </div>
                    <div className={classes.inputContainer}>
                        <input type="text" disabled value="Just text" />
                    </div>
                    <div className={classes.inputContainer}>
                    <Autocomplete
                        inputProps={{placeholder: "test"}}
                        getItemValue={(item) => item.Make + ' ' + item.Model}
                        items={items}
                        renderItem={(item, isHighlighted) =>
                            <div style={{ background: isHighlighted ? '#000' : '#444', padding: '5px', cursor: 'pointer' }}>
                            {item.Make + ' ' + item.Model}
                            </div>
                        }
                        value={selectedName}
                        onSelect={(val, item) => {setSelected(item.VIN); setSelectedName(val)}}
                        />
                    </div>
                </div>
                {/* <input type='text' placeholder='Vhicle default' className='vhicleDefault vhicleSearch'/> */}
                {/* <input type='text' placeholder='Vhicle default' className='vhicleSecond vhicleSearch'/> */}
                <div className={classes.inputContainer + " " + classes.compareContainer}>
                    <button>Compare</button>
                </div>
            </div>
        </div>
    )
}